@import url(https://fonts.googleapis.com/css?family=Comfortaa&display=swap);
* {
    font-family:  'Comfortaa', cursive;
}
.app-header {
    padding: 10px;
    text-align: center;
}

.app-header .last-updated-at {
    font-size: 14px;
}
.footer-container {
    text-align: center;
}
.country-table-cell {
    font-size: 0.7rem;
}

.country-table-header {
    font-size: 0.8rem;
}

@media (max-width: 575px) {
    .country-table-cell {
        font-size: 0.6rem;
    }

    .country-table-header {
        font-size: 0.7rem;
    }
}
.country-table-title {
    text-align: center;
    font-size: 1.1rem;
    font-weight: 500;
    padding: 10px;
}

@media (max-width: 767px) {
    .country-table .rdt_Pagination {
        justify-content: left;
    }
}
.totals-card {
    text-align: center;
}
.totals-card-title {
    font-size: 16px;
    font-weight: 600;
}

.totals-card-count {
    font-size: 20px;
    font-weight: 600;
}

@media (min-width: 576px) and (max-width: 767px) {
    .totals-card-title {
        font-size: 12px;
    }

    .totals-card-count {
        font-size: 16px;
    }
}

@media (max-width: 575px) {
    .totals-card-container {
        width: auto;
        padding-right: 0.3em;
        padding-left: 0.3em;
    }
    .totals-card-title {
        font-size: 0.6rem;
        padding: 0;
    }

    .totals-card-count {
        font-size: 0.8rem;
        padding: 0;
    }
}
.page-container {
    margin-top: 20px;
}

.color-confirmed {
    color: #084177;
}

.color-active {
    color: #F76ABC;
}

.color-new {
    color: #1EB2A6;
}

.color-critical {
    color: #FF0000;
}

.color-deaths {
    color: #A9A9A9;
}

.color.recovered {
    color: #008000;
}
